<template>
  <div class="loginReg">
    <div class="banner" :style="{'background-image':`url(${baseUrl}/public/img/lr.png)`}">
      <i class="iconfont" @click="back">&#xe6b6;</i>
      <div class="logo">
        <img :src="src" alt="">
        <span>每日美食</span>
      </div>
      <div class="style">
        <div :class="style=='login'?'loginBtn sel':'loginBtn'" @click="toggle">登录</div>
        <div :class="style=='reg'?'reg sel':'reg'" @click="toggle">注册</div>
      </div>
    </div>
    <div class="reg box" v-if="style=='reg'">
      <ul>
        <li><input type="text" placeholder="昵称" v-model = "userName"></li>
        <li><input type="text" placeholder="密码" v-model = "passWord"></li>
        <li><input type="text" placeholder="验证码" v-model = "codeInput" class="codeInput">
          <span class="code">{{codeInit}}</span><span @click="getCode" class="codeBtn">看不清，换一张</span>
        </li>
      </ul>
      <div class="btn" @click = "reg">
        <span>注册</span>
      </div>
    </div>
    <div class="login box" v-else>
      <ul>
        <li><input type="text" placeholder="昵称" v-model = "userName"></li>
        <li><input type="text" placeholder="密码" v-model = "passWord"></li>
        <li><input type="text" placeholder="验证码" v-model = "codeInput" class="codeInput">
          <span class="code">{{codeInit}}</span><span @click="getCode" class="codeBtn">看不清，换一张</span>
        </li>
      </ul>
      <div class="btn" @click = "login">
        <span>登录</span>
      </div>
    </div>
  </div>
</template>
<script>
import { reg, login } from 'api/api'
import { modifyLocalStorage } from 'utils/handleData'
export default {
  data () {
    return {
      baseUrl: this.baseUrl,
      src: this.baseUrl + '/public/img//logo.png',
      style: 'login',
      userName: '',
      passWord: '',
      codeInput: '',
      codeInit: '',
      code: ''
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    toggle () {
      if (this.style === 'reg') {
        this.style = 'login'
      } else {
        this.style = 'reg'
      }
      this.getCode()
    },
    getCode () {
      const code = parseInt(Math.random() * (9999 - 1111 + 1) + 1111)
      this.codeInit = code.toString()
    },
    reg () {
      if (!this.userName) { return alert('昵称不能为空') }
      if (!this.passWord) { return alert('密码不能为空') }
      if (this.codeInput !== this.code) {
        alert('验证码错误')
        return false
      }
      reg(this.userName, this.passWord)
        .then((res) => {
          if (res.err === 0) {
            modifyLocalStorage(false, { userName: this.userName, passWord: this.passWord })
            const result = confirm('注册成功，请登录')
            if (result) {
              this.style = 'login'
            } else { this.$router.push('/my') }
          } else {
            alert(res.msg)
          }
        })
    },
    login () {
      if (this.codeInput !== this.code) {
        alert('验证码错误')
        return false
      }
      login(this.userName, this.passWord)
        .then((res) => {
          if (res.err === 0) {
            alert('登录成功')
            modifyLocalStorage(true, res.data)
            this.$router.push('/my')
            this.$router.go(0)
          } else {
            alert('该用户不存在')
          }
        })
    }
  },
  watch: {
    codeInit () {
      this.code = this.codeInit
    }
  },
  mounted () {
    this.getCode()
  }
}
</script>
<style lang="less" scoped>
@import '~style/index.less';
.loginReg{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  background: #fff;
  .banner{
    height: 4.42rem;
    background-size: cover;
    background-position: center center;
    position: relative;
    .iconfont{
      font-size: .34rem;
      line-height: 0.88rem;
      position: absolute;
      top: 0;
      left: .3rem;
    }
    .logo{
      height: 100px;
      width: 50%;
      position: absolute;
      left: 0;
      right: 0;
      top: .3rem;
      margin: auto;
      display: flex;
      align-items: center;
      justify-content: center;
      img{
        width: 1rem;
      }
      span{
        font-size: .48rem;
        line-height: 0.58rem;
        margin-left: 0.3rem;
      }
    }
    .style{
      display: flex;
      justify-content: center;
      height: .8rem;
      position: absolute;
      left: 0;
      right: 0;
      bottom: -.4rem;
      border-radius: .4rem;
      div{
        width: 2.8rem;
        height: .8rem;
        border-radius: .4rem;
        line-height: 0.8rem;
        text-align: center;
        background: #fff;
        color: #808080;
        box-shadow: 0 .08rem .5rem .1rem #dddedf;
      }
      .reg{
        margin-left: -.5rem;
      }
      .sel{
        background: linear-gradient(90deg, @orange2 0%, @orange 100%);
        color: #fff;
        position: relative;
        z-index: 1;
      }
    }
  }
  .box{
    ul{
      width: 4.8rem;
      margin: 1rem auto 0;
      li{
        height: 1.15rem;
        border-bottom: 1px solid #ededed;
        display: flex;
        align-items: center;
        .codeInput{
          width: 1.6rem;
        }
        .code{
          color: @orange;
          margin: .21rem;
          padding: .1rem;
          background: linear-gradient(90deg,@orange2 0%,@orange4 100%);
        }
      }
    }
    .btn{
      width: 4.8rem;
      height: 0.8rem;
      line-height: 0.8rem;
      text-align: center;
      border-radius: .4rem;
      color: #fff;
      background: @orange;
      margin: 1rem auto 0;
    }
  }
}
</style>
